*,
*::before,
*::after {
  box-sizing: border-box;
  color: inherit;
}
img {
  max-width: 100%;
  display: block;
}
a {
  text-decoration: none;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  width: 100%;
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 64px;
}
@media (max-width: 1279px) {
  .container {
    max-width: 768px;
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 320px;
    padding: 0 16px;
  }
}
.page {
  height: 100%;
  font-size: 16px;
  line-height: 1.4;
  color: #243746;
  font-family: "Source Sans Pro", "Arial", sans-serif;
  overflow-x: hidden;
}
.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #fff;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
}
.company-details {
  margin: 0;
  display: flex;
  font-size: 14px;
  line-height: 19.6px;
}
.company-details__item {
  position: relative;
  padding-left: 32px;
}
.company-details__item:not(:last-child) {
  margin-right: 48px;
}
@media (max-width: 1279px) {
  .company-details__item:not(:last-child) {
    margin-right: 24px;
  }
}
@media (max-width: 767px) {
  .company-details__item:not(:last-child) {
    margin-right: 16px;
  }
}
.company-details__icon {
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
}
.company-details__term {
  font-weight: 600;
}
.company-details__description {
  margin: 0;
}
.page-header {
  z-index: 5;
}
.page-header__top-nav-list {
  background-color: #189cd8;
}
@media (max-width: 767px) {
  .page-header__top-nav-list {
    background-color: transparent;
    background-image: linear-gradient(to bottom, #189cd8 50%, transparent 50%);
  }
}
.page-header__container {
  padding-top: 28px;
  padding-bottom: 34px;
  display: flex;
  align-items: center;
}
@media (max-width: 1279px) {
  .page-header__container {
    padding-top: 24px;
    padding-bottom: 32px;
  }
}
@media (max-width: 767px) {
  .page-header__container {
    padding-top: 0px;
    padding-bottom: 16px;
    flex-wrap: wrap;
  }
}
@media (max-width: 767px) {
  .page-header__company-details {
    margin-top: 16px;
    width: 100%;
    justify-content: space-between;
  }
}
.page-header__logo {
  display: block;
  margin-right: 80px;
  min-width: max-content;
}
@media (max-width: 1279px) {
  .page-header__logo {
    margin-right: 40px;
  }
}
@media (max-width: 767px) {
  .page-header__logo {
    order: -2;
  }
}
@media (max-width: 1279px) {
  .page-header__logo img {
    width: 130px;
    height: 62px;
  }
}
@media (max-width: 767px) {
  .page-header__logo img {
    width: 102px;
    height: 49px;
  }
}
.page-header__feedback-button {
  margin-left: auto;
}
@media (max-width: 1279px) {
  .page-header__feedback-button span {
    max-width: 104px;
    text-align: start;
  }
}
@media (max-width: 767px) {
  .page-header__feedback-button {
    order: -1;
  }
  .page-header__feedback-button span {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    -webkit-clip-path: inset(100%);
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}
.page-header__feedback-icon {
  margin-right: 10px;
}
@media (max-width: 767px) {
  .page-header__feedback-icon {
    margin-right: 0;
  }
}
.page-nav {
  background-color: rgba(67, 187, 199, 0.1);
  display: flex;
  justify-content: center;
}
@media (max-width: 767px) {
  .page-nav {
    position: relative;
  }
}
.page-nav__container {
  position: relative;
}
.page-nav__list {
  list-style: none;
  display: flex;
  justify-content: center;
  padding: 12px 0;
  margin: 0;
}
@media (max-width: 767px) {
  .page-nav__list {
    scroll-snap-type: x mandatory;
    overflow-x: scroll;
    justify-content: flex-start;
  }
}
.page-nav__item {
  position: relative;
  min-width: max-content;
}
.page-nav__item:not(:last-child) {
  margin-right: 64px;
}
@media (max-width: 1279px) {
  .page-nav__item:not(:last-child) {
    margin-right: 40px;
  }
}
@media (max-width: 767px) {
  .page-nav__item {
    position: inherit;
  }
  .page-nav__item:not(:last-child) {
    margin-right: 16px;
  }
}
.page-nav__button {
  border: 0;
  background-color: transparent;
  display: flex;
  align-items: center;
  cursor: pointer;
}
.page-nav__button * {
  pointer-events: none;
}
.page-nav__icon {
  margin-left: 10px;
  color: #189cd8;
}
.page-nav__item--current .page-nav__button {
  color: #189cd8;
}
.page-nav__extra-list {
  position: absolute;
  top: 100%;
  width: max-content;
  max-width: 290px;
  list-style: none;
  padding: 20px 32px 16px 32px;
  background-color: #ECF8F9;
  z-index: 1;
  border-radius: 0px 0px 8px 8px;
}
.page-nav__extra-list--hidden {
  visibility: hidden;
}
.page-nav__extra-list--shown {
  visibility: visible;
}
@media (max-width: 767px) {
  .page-nav__extra-list {
    right: 50%;
    transform: translateX(50%);
    width: 100%;
    max-width: 100%;
  }
}
.page-nav__extra-item:not(:last-child) {
  margin-bottom: 16px;
}
.page-nav__extra-item--current {
  color: #189cd8;
}
.page-nav__link--current {
  color: #189cd8;
}
.button {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  border: none;
  padding: 14.5px 32px;
  border: 2px solid #fff;
  background-color: transparent;
  border-radius: 24px;
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  text-align: center;
}
.button--feedback {
  border-color: #189cd8;
  color: #189cd8;
  line-height: 22px;
  font-weight: 600;
  padding: 12.5px 32px;
}
.button--more::after {
  content: "";
  display: block;
  width: 24px;
  min-width: 24px;
  height: 19px;
  margin-left: 4px;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("data:image/svg+xml,%3Csvg width='9' height='16' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.436 7.41s2.132-3.422 1.092-5.223C4.488.386 4.655.69 4.655.69S4.296 0 3.699 0H.015l4.421 7.41Z' fill='%23fff'/%3E%3Cpath d='M4.973 7.766c.39-.648.723-1.336.995-2.055.255-.645.415-1.33.474-2.031l2.305 3.857a.835.835 0 0 1-.002.934l-4.017 6.722a4.301 4.301 0 0 1-.131.22c-.126.193-.441.587-.895.587H0l4.973-8.234Z' fill='%23fff'/%3E%3C/svg%3E%0A");
}
.button--view {
  border: 0;
  background-color: #189cd8;
  box-shadow: 0px 74px 80px rgba(115, 115, 115, 0.13), 0px 47.963px 46.8519px rgba(115, 115, 115, 0.0987037), 0px 28.5037px 25.4815px rgba(115, 115, 115, 0.078963), 0px 14.8px 13px rgba(115, 115, 115, 0.065), 0px 6.02963px 6.51852px rgba(115, 115, 115, 0.051037), 0px 1.37037px 3.14815px rgba(115, 115, 115, 0.0312963);
}
.button--view svg {
  display: block;
  margin-right: 5px;
}
.locale-list {
  color: #fff;
  line-height: 20px;
  font-size: 14px;
  list-style: none;
  background-color: #189cd8;
  margin: 0;
  position: absolute;
  right: 64px;
  top: 0;
  padding: 48px 16px 8px 16px;
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
}
.locale-list--hidden {
  visibility: hidden;
}
.locale-list--shown {
  visibility: visible;
}
@media (max-width: 1279px) {
  .locale-list {
    right: 30px;
  }
}
@media (max-width: 767px) {
  .locale-list {
    padding-top: 40px;
  }
}
.locale-list__item:not(:last-child) {
  margin-bottom: 8px;
}
.locale-list__item--current {
  position: absolute;
  right: 0;
  top: 14px;
  display: flex;
  align-items: center;
  cursor: pointer;
  visibility: visible;
}
@media (max-width: 767px) {
  .locale-list__item--current {
    top: 10px;
  }
}
.locale-list__icon {
  margin-left: 10px;
}
.top-nav-list {
  font-size: 14px;
  color: #fff;
  line-height: 20px;
  list-style: none;
  padding-top: 14px;
  padding-bottom: 14px;
  display: flex;
  justify-content: center;
}
@media (max-width: 1279px) {
  .top-nav-list {
    justify-content: flex-start;
  }
}
@media (max-width: 767px) {
  .top-nav-list {
    padding-top: 0;
    padding-bottom: 0;
    display: grid;
    grid-template-columns: 1fr auto;
  }
}
.top-nav-list__item--current {
  color: #243746;
}
.top-nav-list__item:not(:last-child) {
  margin-right: 32px;
}
@media (max-width: 767px) {
  .top-nav-list__item {
    padding: 10px 0;
  }
  .top-nav-list__item:nth-child(2),
  .top-nav-list__item:last-child {
    grid-row: 2/3;
    color: #243746;
  }
  .top-nav-list__item--current:nth-child(2),
  .top-nav-list__item--current:last-child {
    color: #189cd8;
  }
  .top-nav-list__item:not(:last-child) {
    margin-right: 0;
  }
}
.social-list {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0;
  margin: 0;
}
.social-list__item:not(:last-child) {
  margin-right: 8px;
}
.social-list__link {
  position: relative;
  display: block;
  width: 48px;
  height: 48px;
  border-radius: 8px;
  background-color: #243746;
}
.social-list__icon {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.page-footer {
  background-color: #243746;
  color: #fff;
}
.page-footer__nav {
  display: flex;
  justify-content: center;
  padding-top: 80px;
  padding-bottom: 80px;
}
@media (max-width: 1279px) {
  .page-footer__nav {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding-top: 40px;
    padding-bottom: 40px;
  }
}
@media (max-width: 767px) {
  .page-footer__nav {
    display: flex;
    flex-direction: column;
    padding: 24px 50px;
  }
}
.page-footer__nav-item {
  max-width: 225px;
}
.page-footer__nav-item:not(:last-child) {
  margin-right: 80px;
}
@media (max-width: 1279px) {
  .page-footer__nav-item:not(:last-child) {
    margin-right: 32px;
  }
}
@media (max-width: 767px) {
  .page-footer__nav-item:not(:last-child) {
    margin-right: 0;
  }
}
.page-footer__nav-term {
  margin: 0;
  margin-bottom: 16px;
  font-size: 20px;
  line-height: 28px;
  font-weight: 600;
  display: flex;
  align-items: center;
}
@media (max-width: 1279px) {
  .page-footer__nav-term {
    font-size: 16px;
    line-height: 1.4;
    margin-bottom: 8px;
    width: max-content;
  }
}
.page-footer__nav-icon {
  margin-left: 20px;
  transform: rotate(90deg);
  pointer-events: none;
  display: none;
}
@media (max-width: 767px) {
  .page-footer__nav-icon {
    display: block;
  }
}
.page-footer__nav-description {
  margin: 0;
  margin-bottom: 4px;
  color: #949699;
}
.page-footer__nav-description:hover {
  color: #fff;
}
@media (max-width: 1279px) {
  .page-footer__nav-description {
    font-size: 14px;
  }
}
@media (max-width: 767px) {
  .page-footer__nav-term ~ .page-footer__nav-description {
    display: none;
  }
  .page-footer__nav-term--shown ~ .page-footer__nav-description {
    display: block;
  }
  .page-footer__nav-term--shown .page-footer__nav-icon {
    transform: rotate(-90deg);
  }
}
.page-footer__inner {
  background-color: #189cd8;
}
.page-footer__container {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  align-content: center;
  justify-content: center;
  padding: 28px 0;
}
@media (max-width: 1279px) {
  .page-footer__container {
    flex-direction: column;
    padding: 24px 0;
  }
}
.page-footer__company-details {
  margin-right: 112px;
}
@media (max-width: 1279px) {
  .page-footer__company-details {
    margin-right: 0;
    order: 2;
    margin-top: 24px;
  }
}
@media (max-width: 767px) {
  .page-footer__company-details {
    display: none;
  }
}
.page-footer__copyright {
  font-size: 11px;
  line-height: 15px;
  text-transform: uppercase;
  text-align: center;
  white-space: pre-line;
  padding: 24px 0;
}
.page-footer__copyright span {
  color: #189cd8;
}
.banner {
  width: 100vw;
  color: #fff;
}
.banner__overlay {
  position: absolute;
  right: 270px;
  top: 0;
  width: 100vw;
  height: 520px;
}
.banner__slide {
  position: relative;
  height: 520px;
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
  background-color: lightgray;
  display: flex;
  align-items: center;
}
.banner__slide::before {
  content: "";
  position: absolute;
